import axios from 'axios';
import Elevator from "@/models/Elevator";
import Pusher from 'pusher-js';
import Constants from "@/constants";
import { ref } from 'vue';


class ElevatorMovementsService{
    pusher = null;
    pusherChannel = null;

    constructor() {
        this.pusher = new Pusher(Constants.PUSHER_APP_KEY, { cluster: Constants.PUSHER_CLUSTER })
        this.pusherChannel = this.pusher.subscribe('elevator-movements')
        console.log("pusher configured", this.pusher);
    }

    async getElevators() {
        const data = await axios.get(Constants.BACKEND_BASE_URL + Constants.BACKEND_BASE_REST_URL_VERSION + "/elevators");
        const response = data.data;

        if(response.data == null) {
            return [];
        }

        const elevatorList = response.data.map((data) => {
            const elevator = new Elevator();
            elevator.mapFromModel(data);
            return ref(elevator);
        })

        elevatorList.forEach(elevatorRef => {
            this.subscribeToPusherEvents(elevatorRef)
        })

        return elevatorList;
    }

    subscribeToPusherEvents(elevatorRef) {

        console.log("bound to events :", 'elevator-' + elevatorRef.value.elevatorTag)
        this.pusherChannel.bind('elevator-' + elevatorRef.value.elevatorTag, data => {
            console.log("Elevator event: ", data)
            elevatorRef.value.mapFromModel(data);
        })
    }

    async callElevator(elevatorId, floorNumber) {
        const data = await axios.post(Constants.BACKEND_BASE_URL + Constants.BACKEND_BASE_REST_URL_VERSION + "/call-elevator", {
            elevatorId,
            floorNumber
        });
        return data.data;
    }
}

export default new ElevatorMovementsService();
