<template>
    <div v-if="loading" class="loader full-viewport-size">
        <img src="https://cdnjs.cloudflare.com/ajax/libs/semantic-ui/0.16.1/images/loader-large.gif" alt="loader">
    </div>

    <div v-else-if="error_message" class="full-viewport-size">
        <h3>{{ error_message }}</h3>
    </div>

    <main>
        <div class="album py-5 bg-light">
            <div class="container">
                <div class="row row-cols-1 row-cols-sm-2 row-cols-md-3 g-3">
                    <ElevatorCard v-for="elevator in elevators" v-bind:key="elevator.id" v-bind:elevator="elevator"/>
                </div>
            </div>
        </div>
    </main>
</template>

<script>
import elevatorMovementsService from "@/services/ElevatorMovementsService";
import ElevatorCard from "@/components/ElevatorCard.vue";
import { defineComponent, onMounted, ref, shallowRef } from 'vue';

export default defineComponent({
    name: 'HomeComponent',
    components: { ElevatorCard },
    props: {
        msg: String
    },
    setup() {
        const elevators = shallowRef(0);
        const loading = ref(false);
        const error_message = ref(null);

        onMounted(async () => {
            loading.value = true;

            elevators.value = await elevatorMovementsService.getElevators();

            loading.value = false;
        });

        return {
            elevators,
            loading,
            error_message
        }
    }
});
</script>

