<template>
    <div class="col">
        <div class="card shadow-sm">
            <div class="card-header">Elevator {{ elevator.value.elevatorTag }}</div>
            <div class="card-body">

                <ul class="list-group list-group-flush text-start">
                    <li class="list-group-item">
                        <span class="badge bg-secondary">Floor</span>
                        {{ elevator.value.floor }}
                    </li>
                    <li class="list-group-item">
                        <span class="badge bg-secondary">State</span>
                        {{ elevator.value.state }}
                    </li>
                    <li class="list-group-item">
                        <span class="badge bg-secondary">DoorState</span>
                        {{ elevator.value.doorState }}
                    </li>
                    <li class="list-group-item">
                        <span class="badge bg-secondary">Direction</span>
                        {{ elevator.value.direction }}
                    </li>
                </ul>
            </div>

            <div class="card-footer">
                <div class="row d-flex justify-content-around align-items-center">
                    <div class="col-auto">
                        <label for="floorNumber" class="col-form-label">Floor calling from</label>
                        <input type="text" id="floorNumber" class="form-control"
                               aria-describedby="passwordHelpInline"
                               v-model="floorNumber">
                    </div>
                    <button type="button" class="btn btn-primary col-auto" @click="callElevator">Call Elevator</button>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
import elevatorMovementsService from "@/services/ElevatorMovementsService";
import { defineComponent, ref } from 'vue';

export default defineComponent({
    name: 'ElevatorCard',
    props: ['elevator'],
    setup(props) {
        const floorNumber = ref(0);
        const loading = ref(false);
        const error_message = ref(null);

        const callElevator = async() => {
            loading.value = true;

            const response = await elevatorMovementsService.callElevator(
                props.elevator.value.elevatorTag, floorNumber.value);

            loading.value = false;
            if (response.data) {
                error_message.value = response.errorMessage;
            }
        }

        return {
            loading,
            error_message,

            callElevator,
            floorNumber
        }
    }
});
</script>
