
export default class Elevator {
    id = -1;
    elevatorTag = 0;
    floor = 0;
    state = "stationary";
    direction = "none";
    doorState = "closed";

    mapFromModel(model) {
        this.id = model.id;
        this.elevatorTag = model.elevatorTag;
        this.direction = model.direction;
        this.floor = model.floor;
        this.state = model.state;
        this.doorState = model.doorState;
    }
}
